<template lang="html">
  <div :class="[btnClass, cname]">
    <slot/>
  </div>
</template>

<script>
export default {
  data(){
    return {
      btnClass: 'btn',
    }
  },
  props: {
    cname: {
      type: String,
      default: "",
    },
  },

}
</script>

<style lang="scss">
@import "../../css/element.scss";
.btn{
  @include btn;
}
</style>



